	onStoryChange (e) {
    this.setState({ activeStory: e.currentTarget.dataset.story, activePanel: "home" })
	}
	
	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					//fetch('https://topbotbackend.herokuapp.com/')
					fetch('https://ivandanilov.pythonanywhere.com/users')
					.then(response => response.json())
					.then(users => {
						users = users.items;
						var tmp =  users.sort((a,b) => a.message_today - b.message_today)
						tmp = tmp.reverse()
						this.setState({
							users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_today; return tmp;}),
							me: tmp.find(x => x.user_id === e.detail.data.id) ? 
							tmp.find(x => x.user_id === e.detail.data.id).message_today : 0,
							spinner: null
						})
					})
					.catch(err => {
						console.log(err)
						this.setState({spinner: null})
					})
					this.setState({ fetchedUser: e.detail.data });
					this.getHelpers()
					fetch('https://ivandanilov.pythonanywhere.com/service/'+this.state.fetchedUser.id)
						.then(response => response.json())
						.then(response => {
							try { var onb = response.items[0].onboarding; }
							catch(e) { this.setState({activePanel: 'guide', activeStory: 'guide'}); return; }
							if (onb) {
								//this.setState({activePanel: 'home', activeStory: 'manage'});
								this.openchats();
							} else {
								this.setState({activePanel: 'guide', activeStory: 'guide'});
							}
						})
						.catch(err => {
							console.log(err)
						})
					break;
					case 'VKWebAppUpdateConfig':
						let schemeAttribute = document.createAttribute('scheme');
						schemeAttribute.value = e.detail.data.scheme ? e.detail.data.scheme : 'client_light';
						document.body.attributes.setNamedItem(schemeAttribute);
					break;
					case 'VKWebAppAccessTokenReceived':
						this.setState({popout: <ScreenSpinner />})
						fetch("https://ivandanilov.pythonanywhere.com/admins")
						.then(response => response.json())
						.then(data => {
							var chat_arr = data.items.map((chat, i) => {
								if(chat.user_id === this.state.fetchedUser.id){
									return {chat_id: chat.chat_id, lvl: chat.level}
								}
							})
							chat_arr = chat_arr.filter(function (el) {
								return el != null;
							});
							if(chat_arr.length === 0){
								this.setState({
									popout: null,
									auth: true
								})
							}else{
								fetch("https://ivandanilov.pythonanywhere.com/chats")
								.then(response => response.json())
								.then(data => {
									var chats = [];
									for(var i = 0; i < chat_arr.length; i++){
										for(var j = 0; j < data.items.length; j++){
											if(data.items[j].chat_id === chat_arr[i].chat_id){
												var obj = data.items[j];
												obj.level = chat_arr[i].lvl;
												chats.push(obj)
											}
										}
										if(i === chat_arr.length-1){
											this.setState({
												popout: null,
												chats: chats,
												auth: true
											})
										}
									}
								})	
							}
						})
						break;
					case 'VKWebAppAccessTokenFailed':
						this.setState({ popout:
							<Alert
								actions={[{
									title: '',
									autoclose: true,
									style: 'cancel'
								}]}
								onClose={this.closePopout}
							>
								<h2>  </h2>
								<p> .   .</p>
							</Alert>
						});
						break;
						case 'VKWebAppCallAPIMethodResult':
							let users = {};
							for (var i = 0; i < e.detail.data.response.length; i++) {
								users[e.detail.data.response[i].id] = e.detail.data.response[i];
							}
							this.setState({banlist: users})//, activeStory: 'banlist', activeStory: 'banlist'});
							break;
				default:
					console.log(e.detail.type)
			}
		});
		this.setState({spinner: <ScreenSpinner />})
		connect.send('VKWebAppGetUserInfo', {});
	}

